import "./App.css";
import io from "socket.io-client";
import { useState } from "react";
import { Chat } from "./Chat";
import SignIn from "./SignIn";
const socket = io.connect("https://mynodejschatapp.vercel.app");

function App() {
  const [username, setUsername] = useState("");
  const [room, setRoom] = useState("");
  const [isRoomCreated, setIsRoomCreated] = useState(false);

  const joinRoom = () => {
    if (username !== "" && room !== "") {
      socket.emit("join_room", room);
      setIsRoomCreated(true);
    } else {
      alert("Please enter Username and roomID");
    }
  };
  return (
    // <div className="App">
    //   <h3>Join a Chat</h3>
    //   <input type='text' placeholder='Enter Username' onChange={(e) => setUsername(e.target.value)}/>
    //   <input text='text' placeholder='Enter Room Id' onChange={(e) => setRoom(e.target.value)}/>

    //   <button onClick={joinRoom}>Join a Room</button>
    //   <Chat socket={socket} username={username} room={room}/>
    // </div>
    <>{isRoomCreated ? <Chat socket={socket} username={username} room={room}/> : <SignIn username={username} setUsername={setUsername} room={room} setRoom={setRoom} joinRoom={joinRoom} />}</>
  );
}

export default App;
