import React, { useState, useEffect } from "react";
import styles from "@chatscope/chat-ui-kit-styles/dist/default/styles.min.css";
import { MainContainer, ChatContainer, MessageList, Message, MessageInput } from "@chatscope/chat-ui-kit-react";

export const Chat = ({ socket, username, room }) => {
    const [messages, setMessages] = useState([]);
    const sendMessage = async (message) => {
        if (message !== "") {
            const messageData = {
                room: room,
                auther: username,
                message: message,
                time: new Date(Date.now()).getHours() + ":" + new Date(Date.now()).getMinutes(),
            };
            await socket.emit("send_message", messageData);
            const temp = JSON.parse(sessionStorage.getItem("messages")) || [];
            temp.push({ direction: "outgoing", message: message, sentTime: new Date(Date.now()).getHours() + ":" + new Date(Date.now()).getMinutes(), sender: username });
            sessionStorage.setItem("messages", JSON.stringify(temp));
            setMessages(temp);
        }
    };

    useEffect(() => {
        socket.on("receive_message", (data) => {
            const temp = JSON.parse(sessionStorage.getItem("messages")) || [];
            temp.push({ direction: "incoming", message: data?.message, sentTime: data?.time, sender: data?.auther });
            sessionStorage.setItem("messages", JSON.stringify(temp));
            setMessages(temp);
        });
    }, [socket]);
    return (
        <>
            <div style={{ position: "relative", height: "500px" }}>
                <MainContainer>
                    <ChatContainer>
                        <MessageList>
                            {messages?.map((item, i) => (
                                <Message
                                    key={i}
                                    model={{
                                        direction: item?.direction,
                                        message: item?.message,
                                        sentTime: item?.sentTime,
                                        sender: item?.sender,
                                    }}
                                />
                            ))}
                        </MessageList>
                        <MessageInput placeholder="Type message here" onSend={sendMessage} />
                    </ChatContainer>
                </MainContainer>
            </div>
            ;
        </>
    );
};
